import React, { Component} from 'react';
import PropTypes from 'prop-types';//used by react version >1.5 
import '../res/css/home.css';



class DataItem extends Component{
    
    //构造函数
    constructor(props){
        super(props);
        this.state = {
        }
    }

    /**
    * 设置默认值
    */
    static get defaultProps(){
        return {
            dataItem: null,
            aid: 0,
            sendRedPacketListener: (walletAddress, money)=>{},
        }
    }
    /**
    * 指定属性类型
    */
   static propTypes = {
        dataItem: PropTypes.object.isRequired,
        aid: PropTypes.number.isRequired,
        sendRedPacketListener: PropTypes.func.isRequired,
    }

    showInputDialog(){
        
    }

    /**
        birthItem.id = this.repo_id;
        birthItem.name = b_name;
        birthItem.category= b_category;
        birthItem.gender = b_gender;
        birthItem.relationship = b_relationship;
        birthItem.phone = b_phone;
        birthItem.hisWalletAddress = b_hisWalletAddress;
        birthItem.birthday = b_birthday;
        birthItem.createTime = b_createTime;
     */

    drawUI(trStyle){
        return(
            <tr key={'tr_' +this.props.dataItem.id}  className={trStyle}>
                <th align="center">{this.props.aid}</th>
                <td align="center">{this.props.dataItem.gender === "male" ? 
                <img src={require("../res/images/home/icon_male.png")} /> :
                <img src={require("../res/images/home/icon_fmale.png")} /> 
                }
                    
                </td>
                <td>{this.props.dataItem.name}</td>
                <td>{this.props.dataItem.gender}</td>
                <td>{this.props.dataItem.relationship}</td>
                <td>{this.props.dataItem.birthday}</td>
                <td>{this.props.dataItem.phone}</td>
                <td>{this.props.dataItem.desc}</td>
                <th style={{minWidth: 250}} className="App">
                {(this.props.dataItem.hisWalletAddress === null 
                    || this.props.dataItem.hisWalletAddress === ""
                    || this.props.dataItem.hisWalletAddress === undefined) ?
                
                    <div style={{minWidth: 250}} className="App">无钱包地址</div> 
                    :
                    //autoFocus={this.props.aid === 1 ? true : false}
                    <button className="css_home_button css_home_button_add" onClick={()=>{
                        var money;
                        var value=prompt("请输入发送红包Nas币数","0.01");
                        console.log("send red packet value = "+value);
                        if (value !== null && value !== ""){
                            money = value;
                            this.props.sendRedPacketListener(this.props.dataItem.hisWalletAddress, money);
                        }
                        
                    }}>发红包祝福</button>
                
                }</th>

            </tr>
        );
    }

    //渲染
    render(){
         //定义属性
         const{
            isChecked,
            dataItem,
            aid,      
            sendRedPacketListener,
        } = this.props;

        return(
            // 定义不同的tr 色
            this.props.aid % 2 === 0 ? this.drawUI("css_home_evenrowcolor") : this.drawUI("css_home_oddrowcolor")
        )

       
    }
}

export default DataItem;


