 /**
  * 部署合约 host
  */
const DAPP_HTTPREQUEST_HOST_LOCAL_NET = "http://localhost:8086";
const DAPP_HTTPREQUEST_HOST_TEST_NET = "https://testnet.nebulas.io";
const DAPP_HTTPREQUEST_HOST_MAIN_NET = "https://mainnet.nebulas.io";


/**
 * Dapp [数据模块] contract address 对应合约 “ BirthMainagerContract.js”
 */
const LOCALNET_DAPP_CONTRACT_ADDRESS = "";
const TESTNET_DAPP_CONTRACT_ADDRESS = "n1xDtUVPhHFLkg8rSCaWFDLpyQKYw23wPvU";
const MAINNET_DAPP_CONTRACT_ADDRESS = "n1wULn4j635ZDnZU5hehJcYDWnJYFtPbpsH";



/**
 * 账本地址
 */
const ACCOUNT_ADDRESS = "n1Gff8LHyYN7R92CYiqj1xWLodFubQJvHf7";

class AppConfig extends Component{

    /**
     * return 0 is mainnet, 1 is testnet, 2 is localnet
     */
    static getNetType(){
        return 0; 
    }

    /**
     * 获取host
     * @param {*} applyNetType 0 is mainnet, 1 is testnet, 2 is localnet
     */
    static getDappHttpRequestHost() {
        var applyNetType = this.getNetType();
        console.log("getDappHttpRequestHost applyNetType ? "+applyNetType);
        if(applyNetType === 1) {
            return DAPP_HTTPREQUEST_HOST_TEST_NET;
        } else if(applyNetType === 2) {
            return DAPP_HTTPREQUEST_HOST_LOCAL_NET;
        } else {
            return DAPP_HTTPREQUEST_HOST_MAIN_NET;
        }
    }


    /**
     * 合约地址
     */
    static getDappContractAddress() {
        var applyNetType = this.getNetType();
        if(applyNetType === 1) {
            return TESTNET_DAPP_CONTRACT_ADDRESS;
        } else if(applyNetType === 2) {
            return LOCALNET_DAPP_CONTRACT_ADDRESS;
        } else {
            return MAINNET_DAPP_CONTRACT_ADDRESS;
        }
    }



    /**
     * 账本地址
     * User account address
     */
    static getAccountAddress() {
        return ACCOUNT_ADDRESS;
    }


    static getHelpPage() {
        return "https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/";
    }

    render(){
        return;
    }
}

export default AppConfig;


