import React, {Component} from 'react';
import PropTypes from 'prop-types';
import DataItem from './DataItem';
import '../constants/Utils.js';

var table_style ={
    "backgroundColor" : "#d0e4fe",
    "border" : "1",
    "width" : "100%", 
    "cellspacing" : "1", 
    "cellpadding" : "0",
    "color" : "orange",

}
class DisplayList extends Component{

    componentDidMount() {
        console.log("DisplayList---componentDidMount---");
        
    }
    shouldComponentUpdate(NEXTPROPS , NEXTSTATE ){
        return true;
    }

    static get defaultProps(){
        return {
            data_arrays : [],
            sendRedPacketListener: (wallertAddress, money)=>{},
        }
    }
    static propsTypes={
        data_arrays: PropTypes.arrayOf(PropTypes.object).isRequired,
        sendRedPacketListener: PropTypes.func.isRequired,
    }
    render() {
        const {
            data_arrays,
            sendRedPacketListener,
        }= this.props;

        console.log("==> arrays data =  "+data_arrays);

        //区别类型
        var arr_birth = data_arrays.filter((obj)=> obj.category === "0");
        var arr_memorial = data_arrays.filter((obj)=> obj.category === "1");


        if(arr_birth !== null) {
            console.log("==> arrays arr_birth data =  "+arr_birth.length);
        }
        if(arr_memorial !== null) {
            console.log("==> arrays arr_memorial data =  "+arr_memorial.length);
        }

        var num = 0;
        var num_mermorial = 0;
        return (
            <div>
                <h3>生日:</h3>
            <table id="table_all_data_id" className="altrowstable css_hor_center" style={{margin: 20}} >
            <tbody>
                <tr>
                    <th>序号</th>
                    <th>头像</th>
                    <th>名称</th>
                    <th>性别</th>
                    <th>关系</th>
                    <th>日期</th>
                    <th>电话</th>
                    <th>描述</th>
                </tr>
                {
                    arr_birth.map((itemObj)=>{
                        console.log("loop in ---itemObj = ",itemObj);
                        if(itemObj === null) {
                            return;
                        }
                        num++;
                        return (
                            <DataItem key={num}
                                aid={num}  
                                dataItem={itemObj}
                                sendRedPacketListener = {this.props.sendRedPacketListener} />
                            );
                    })
                }
                </tbody>
             </table>


               <h3>纪念日:</h3>
            <table id="table_all_data_id" className="altrowstable css_hor_center" style={{margin: 20}} >
            <tbody>
                <tr>
                    <th>序号</th>
                    <th>头像</th>
                    <th>名称</th>
                    <th>性别</th>
                    <th>关系</th>
                    <th>日期</th>
                    <th>电话</th>
                    <th>描述</th>
                </tr>
                {
                    arr_memorial.map((itemObj)=>{
                        console.log("loop in ---itemObj = ",itemObj);
                        if(itemObj === null) {
                            return;
                        }
                        num_mermorial++;
                        return (
                            <DataItem key={num_mermorial}
                                aid={num_mermorial}  
                                dataItem={itemObj}
                                sendRedPacketListener = {this.props.sendRedPacketListener} />
                            );
                    })
                }
                </tbody>
             </table>
             </div>
      
        )
    }
}
export default DisplayList;


